import axios from "axios";
const apiKey = "68d3c74a18d32691083dc9e313e197f5";

const fetchMoviesTrending = async () => {
  return await axios
    .get(`https://api.themoviedb.org/3/trending/all/day?api_key=${apiKey}`)
    .then((response) => response.data.results);
};
const fetchMoviesSearch = async (query) => {
  return await axios
    .get(
      `https://api.themoviedb.org/3/search/movie?query=${query}&api_key=${apiKey}`
    )
    .then((response) => response.data.results);
};
const fetchMoviesDetails = async (movieId) => {
  return await axios
    .get(`https://api.themoviedb.org/3/movie/${movieId}?api_key=${apiKey}`)
    .then((response) => response.data);
};
const fetchMoviesCast = async (movieId) => {
  return await axios
    .get(
      `https://api.themoviedb.org/3/movie/${movieId}/credits?api_key=${apiKey}`
    )
    .then((response) => response.data.cast);
};
const fetchMoviesReviews = async (movieId) => {
  return await axios
    .get(
      `https://api.themoviedb.org/3/movie/${movieId}/reviews?api_key=${apiKey}`
    )
    .then((response) => response.data.results);
};
export {
  fetchMoviesTrending,
  fetchMoviesSearch,
  fetchMoviesDetails,
  fetchMoviesCast,
  fetchMoviesReviews,
};
