import { Component } from "react";
import { fetchMoviesSearch } from "../services/moviesApi";
import SearchForm from "../components/SearchForm/SearchForm";
import MovieSearch from "../components/MovieSearch/MovieSearch";
class MoviesPage extends Component {
  state = {
    movies: [],
  };
  componentDidMount() {
    const searchMovies = localStorage.getItem("My Movies");
    const parsedMovie = JSON.parse(searchMovies);
    if (searchMovies) {
      this.setState({
        movies: parsedMovie,
      });
    } else {
      this.setState({
        movies: [],
      });
    }
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevState.movies !== this.state.movies) {
      localStorage.setItem("My Movies", JSON.stringify(this.state.movies));
    }
  }
  handleSubmit = (query) => {
    this.getMovies(query);
  };
  getMovies = (query) => {
    fetchMoviesSearch(query).then((results) =>
      this.setState({
        movies: [...results],
      })
    );
  };
  render() {
    const { movies } = this.state;
    return (
      <div>
        <SearchForm onSubmit={this.handleSubmit} />
        <MovieSearch movies={movies} />
      </div>
    );
  }
}
export default MoviesPage;
